.content {
    width: 70%;
    padding-top: 32px; }

.cars {
    margin-top: 48px;
    display: grid;
    grid-template-columns: repeat(2, 369px);
    max-height: 702px;
    overflow: auto;
    &::-webkit-scrollbar-track {
        border-radius: 4px; }
    &::-webkit-scrollbar {
        width: 6px; }
    &::-webkit-scrollbar-thumb {
        width: 10px;
        border-radius: 4px;
        background: #0EC261; } }

.radio {
    width: 12px;
    height: 12px;
    border: 1px solid #999999;
    border-radius: 50%;
    margin-right: 8px; }

.filter {
    display: flex;
    color: #999999; }

.filterItem {
    display: flex;
    align-items: center;
    margin-right: 16px;
    cursor: pointer; }

.active {
    color: #121212;
    .radio {
        border: 3px solid #0EC261; } }
